.editFiles{
    margin-left: 8%;
    margin-top: 8%;
    height : 100vh;
}

.editHeader{
    font-size: 30px;
    color : #7e22ce;
    font-weight: 600;
    margin-bottom: 5%;
}
textarea{
    padding : 30px;
    border: 2px solid black;
    width : 75%;
    height : 30vh;
    margin-left: 3%;
}
textarea button{
    width : 30%;
}
.editMode{
    margin-left: 4%;
    width : 12%;
    height: 30px;
    margin-bottom: 2%;
    border-radius: 30px;
    cursor : pointer;
    background-color: #4f4f4f;
    color: white;
    margin-top: 1%;
}
.buttons{
    display: flex;
    justify-content: space-between;
    width : 80%
}
.queryButton{
    display: flex;
    justify-content: space-between;
    width : 50%;
    
    margin-bottom : 3%;
}
.query{
    width  : 100%;
    border-radius: 10px;
    height : 40px;
    margin-left: 5%;
    cursor: pointer;

}
.save{
    background-color: black;
    color : white;
}
.discard{
    color :red;
    border :2px solid red;
}


@media screen and (max-width : 750px) {
    .editMode{
        width : 25%;
    }
    .save{
        background-color: black;
        width : 100%;
        color : white;
    }
    .discard{
        color :red;
        border :2px solid red;
    }
    .queryButton{
        width : 50%;
    }
}