.popup{
    position: absolute;
    top : 0;
    left :0;
    width : 100%;
    height: 100%;
    background-color: rgba(0,0,0,0.2);
    z-index: 5;
    display: flex;
    justify-content: center;
    align-items: center;
}
.deletepopup{
    position: relative;
    padding : 32px;
    margin: 3%;
    width : 40%;
    max-width : 1000px;
    background-color: white;
    margin-top: 5%;
}
.warning{
    text-align: center;
}
.update-close-btn{
    position: absolute;
    margin-left : 90%;
    border-radius: 30px;
    padding: 0%;
    margin-top: -10px;
}
.popup-inner .close-btn{
    position: absolute;
    top : 16px;
    margin-left: 38%;
}

.clientDetail{
    display: grid;
    grid-template-columns: 25% 75%;
    padding : 4%;
}

.clientDetail .inputClientDetail{
    height: 50px;
    border-radius: 30px;
    text-align: center;
}
.SubmitDetail{
    width : auto;
    height: 30px;
    text-align: center;
    border-radius: 30px;
    font-weight: 600;
    margin-top: 4%;
    margin-right: 10%;
}
.update-SubmitDetail{
    margin-left: 5%;
    border: 1px solid black;
    text-align: center;
    margin-top: 5%;
    height: 35px;
    border-radius: 10px;
    color : white;
    background-color: #7e22ce;
    cursor: pointer;
}
.reason{
    grid-column: span 2;
}
.reasonTextArea{
    width : 80%;
    margin-left : 5%;
}
.ReasonLabel{
    margin-bottom: 50%;
}
.SubmitDetail:hover{
    background-color: green;
    color: white;
    font-weight: 600;
}

.Confirmation{
    margin-top: 5%;
    display: flex;
    justify-content: space-around;
}
.sure{
    width : 20%;
    height : 40px;
    background-color: rgb(69, 161, 218);
    border-radius: 30px;;
}
.No{
    width : 20%;
    height : 40px;
    background-color: rgb(232, 90, 97);
    border-radius: 30px;;
}
@media screen and (max-width : 1500px){
    .popup-inner{
        width : 50%;
    }
    .update-close-btn{
        margin-left: 85%;
        border-radius: 30px;
        padding: 0%;
        margin-top: -10px;
    }
}
@media screen and (max-width : 900px){
    .popup{
        width : 110%;
        height: 110%;
    }
    .popup-inner{
        width : 50%;
    }
    .update-close-btn{
        margin-left: 85%;
        border-radius: 30px;
        padding: 0%;
        margin-top: -10px;
    }
}
@media screen and (max-width : 750px){
    .data{
        display: flex;
    }
    .popup{
        width : 100%;
        height: 100%;
    }
    .clientDetail{
        display: grid;
        grid-template-columns: 35% 65%;
        padding : 4%;
    }
    label{
        width: 50%;;
    }
    .popup-inner{
        width : 70%;
    }

}
@media screen and (max-width : 600px){

    .popup{
        width : 100%;
        height: 100%;
    }
    .popup-inner{
        width : 80%;
        margin-left: 5%;
    }
    .projectt{
        display: flex;
        flex-direction: column;
        margin-left: 10%;
    }
}