.projectDetailPage{
    display : grid;
    height : calc(100vh - 55px);
    grid-template-columns: 18% 82%;
}
.uploadHeader{
    padding : 0px;
    font-weight: 700;
    font-size: 33px;
    color : #7e22ce;
    width: 98%;
    
}
.detailContent{
    margin-left: 3%;
}
.uploadCardContainer{
    /* border: 2px solid red; */
    height  : 30vh;
}
.div1{
    margin: 0%;;
}
.uploadCard{
    margin-top: 50px;
    display: flex;
    justify-content: space-around;
    flex-direction: row;
    width : 98%;
    margin-left: 0%;
}

.allFiles{
    width : 100%;
}
.youtube, .spotify, .rss_feed{
    display: flex;
    width : 20vw;
    
    
    border-radius: 10px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
.uploadContent p{
    margin-bottom: -10px;
    margin : 10px;
}
.youtube img,.spotify img, .rss_feed img{
    width : 65px;
    height: 51px;
    margin-right: 20px;
    margin-top: 10px;
}

.youtube p,.spotify p , .rss_feed p{
    font-size : 15px;
    color: black;
}
.uploadFile img{
    width : 65px;
}
.uploadHeader h3{
    margin-left: 5%;
}
.or{
    margin-left: 50%;
    margin-top : 5%;
}

.outerupload{
    height : 55vh;
}
.uploadFile{
    height : 60%;
    width : 80%;
    margin-left: 10%;
    border-radius: 10px;
    flex-direction: column;
    border: 2px dashed black;
    display: flex;
    align-items: center;
    justify-content: center;
}

.filetype{
    color: grey;
    font-size: small;
    margin-top: -10px;
}
.uploadFilebutton{
    width : 20%;
    height: 18%;
    border-radius: 30px;
    color : #7e22ce;
    border-color: #7e22ce;;
}

/* .detailContent{
    border: 2px solid red;
} */
@media screen and (max-width : 1180px) {
    .detailContent{
        width : 100%;

    }
    .projectDetailPage{
        display : block;
        width : 100%;
    }
    .uploadHeader h3{
        margin-left: 0%;
    }
    
    .uploadCard{
        width  : 100%;
        margin-left: 0%;
    }
    .outerupload {
        width : 100%;
    }
    .youtube, .spotify, .rss_feed{
        width : 25vw;
    }
    
}
@media screen and (max-width : 900px) {
    .uploadCard{
        width : 100%;
    }   
    .outerupload {
        width : 100%;
    }
    .table{
        margin-left: 0%;
    }
  
}
@media screen and (max-width : 820px) {
    
    .div1{
        width : 40%;
    }
    .detailContent{
        width :100%;
    }
    .uploadCardContainer{
        height : 20vh;
    }
    .uploadCard{
        width : 100%;
        flex-wrap: wrap;
        margin-left: 0%;
    }
    .outerupload {
        width : 100%;
        margin-top: 15%;
    }
    
    .youtube, .spotify, .rss_feed{
        width : 45vw;
        margin-right: 5%;   
    }
    .youtube img, .spotify img, .rss_feed img{
        margin-left: 15%;   
    }
}
@media screen and (max-width : 600px) {

    .uploadCard{
        flex-direction: column;
    }
    .div1{
        
        width : 100%;
    }
    .youtube, .spotify, .rss_feed{
        margin-bottom: 10px;
        width : 60%;
        margin-left: 20%;
    }
    .outerupload{
        width : 100%;
        margin-left: 0%;
        margin-top: 45%;
    }
  

}