.homepage{
    height : 100vh;
}
.notification{
    width : 98%;
    height : 10%;
}
.homecontent{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: -4%;
}
.heading p{
    font-size: 50px;
    font-weight: 600;
    color: #7e22ce;
    display: flex;
    justify-content: center;
}
.plus{
    width :50px;
    background-color: white;
    align-items: center;
    border-radius: 30px;
}
.CreateProjectButton{
    font-size: 25px;
    width : 20%;
    height : 50px;
    border: 2px solid rgb(36, 16, 119);;
    text-align: center;
    margin-left: 40%;
    background-color: #211935;
    color: white;
    border-radius : 10px ;
}
.createProject{
    width : 100%;
    height : 10vh;
}
.notification{
    display:  flex;
    justify-content: right;
}
.description p{
    text-align: center;
    font-size: 20px;
    color: grey;
}
.notificationIcon img{
    width :24px;
    padding:10px 20px;
}
.setting{
    
    padding:10px 20px;
}

@media screen and (max-width : 1350px){

    .CreateProjectButton{
        width : 30%;
        margin-left: 35%;
    }

}
@media screen and (max-width : 890px){

    .CreateProjectButton{
        width : 40%;
        margin-left: 35%;
    }

}

@media screen and (max-width : 670px){

    .CreateProjectButton{
        width : 55%;
        margin-left: 25%;
    }

}

@media screen and (max-width : 490px){

    .CreateProjectButton{
        width : 65%;
        margin-left: 20%;
    }
    .createProject{
        width : 120vw;
        margin-left: -10%;
    }
}