
.sideBar{
    background-color: #f3e8ff;
    height : 100%;
    width : 20vw;
    transition: 850rem;
}
.header{
    margin-left : 28px;
}
.numbering{
    background-color: #cac1d5;
    border-radius: 30px;
    padding : 3px;
}
.link{
    text-decoration: none;
}
.Content{
    margin-top:30px ;
    display: grid;
    grid-template-rows: 90% 10%;
}
.content1{
    margin-left: 30px;
}
.projects, .configration, .deployment, .pricing{
    height : 35px;
    background-color: #f3e8ff;
    width : 90%;
    margin-bottom: 10px;
    border : none;
    display: flex;
    align-items: center;
    cursor: pointer;
    margin-left: 10px;
}
.header{
    margin-top: 10%;
    display : grid;
    grid-template-columns: 75% auto 20%;
    justify-content: left;
}
.projects:hover, .configration:hover, .deployment:hover, .pricing:hover{
    background-color: #e2d8ee;
    border-radius: 30px;
    width : 90%;
    border: none;
    color: black;
    
}
.projects:focus, .configration:focus, .deployment:focus, .pricing:focus{
    background-color: #7e22ce;
    border-radius: 30px;
    color: white;
}
.projects:focus .numbering, .configration:focus .numbering, .deployment:focus .numbering, .pricing:focus .numbering{
    background-color: black;
}
.disable{
    display : none;
}
.sideBarexpandIcon{
    display:none;
}
.settingPage{
    font-size: medium;
    background-color: #7e22ce;
    border-radius: 10px;
    display: flex;
    color: white;
    justify-content: center;
    align-items: center;
    text-align: center;
    margin-bottom: 10%;
}
@media screen and (max-width : 1180px) {
    .disable{
        display : none;
    }
    .sideBarexpandIcon{
        display:block;
        background-color: #7e22ce;
        border-radius: 10px;;
    }
    .sideBar{
        z-index: 5;
        position: absolute;
        width : 30%;
        height : calc(100%);
    }
    .shortSideBar{
        width : 3vw;
    }
}

@media screen and (max-width : 760px) {
    .sideBar{
        z-index: 5;
        position: absolute;
        width : 40vw;
        
    }
}
@media screen and (max-width : 600px) {
    .sideBar{
        z-index: 5;
        position: absolute;
        width : 50%;
    }
}

@media screen and (max-width : 450px) {
    .sideBar{
        z-index: 5;
        position: absolute;
        width : 70vw;
    }
}