.projectCard{
    width : 100%;
    display: grid;
    grid-template-columns: 30% 30% 30% ;
    grid-column-gap: 56px;
}
.link{
    
    text-decoration:none;
    color : black;
}
.projectInner{
    border : 1px solid black;
    max-width: 80%;
    display: flex;
    margin-left: 10%;
    border-radius: 10px;
    height : 80%;
    
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);

}
.projectImg{
    width : 35%;
    text-align: center;
    justify-content: center;
    margin : 10px;
    color: white;
    border-radius: 10px;
    
}
.name{
    font-weight: 600;
    font-size: large;
    color : #7e22ce;
    margin-bottom: 10px;;
}
.length{
    margin-top: 9px;
    font-size: 14px;
}
.lastedit{
    color: grey;
    font-size: 14px;
}
.projectContent p{
    margin-bottom: -10px;
}
.projectImg p{
    margin-top: 0px;
    font-size: 40px;
}
.purple {
    background-color: #7e22ce;
}
.orange{
    background-color: orange;
}


@media screen and (max-width : 1180px) {
    .projectCard{
        margin-bottom: 15%;
    }
    .projectInner{
        display: flex;
        flex-direction: column;
        height : 110%;
    }
    .projectImg{
        width : 90%;
    }
    .projectUpper{
        margin-bottom: 15%;;
    }
    .projectContent{
        margin-left: 5%;
    }
}

@media screen and (max-width : 600px) {
    .projectCard{
        width : 100%;
        display: grid;
        grid-template-columns: 50% 50%;
        grid-column-gap: 56px;
    }
    .projectUpper{
        margin-bottom: 25%;;
    }
}