.popup{
    position: fixed;
    top : 0;
    left :0;
    width : 100%;
    height: 100vh;
    background-color: rgba(0,0,0,0.2);
    z-index: 5;
    display: flex;
    justify-content: center;
    align-items: center;
}
.deletepopup{
    position: relative;
    padding : 32px;
    margin: 3%;
    width : 40%;
    max-width : 1000px;
    background-color: white;
    margin-top: 5%;
}
.update-close-btn{
    position: absolute;
    margin-left : 90%;
}
.datainputt{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    border: 2px soild red;
}
.popup-inner{
    position: relative;
    padding : 32px;
    margin: 3%;
    width : 100%;
    background-color: white;
    margin-top: 5%;
    display : flex;
    flex-direction: column;
    border-radius: 10px;
    
}
.createLabel p{
    font-size : 25px;
    font-weight: 500;
}
 .projectInput{
    height: 50px;
    width : 80%;
    border-radius: 10px;
    text-align: center;
}
.projectt{
    display: grid;
    grid-template-columns: 10% 20% 70%;
    margin-bottom: 2%;
}
.Label{
    grid-column-start: 2 ;
}
.createProjectButton{
    display: flex;
    justify-content:right;
    align-items: right;
}


.youtube_logo img{
    width : 55px;
}
.update-cancel-SubmitDetail{
    height: 40px;
    text-align: center;
    align-items: center;
    border: 1px solid black;
    margin-top: 5%;
    height: 35px;
    border-radius: 10px;
    cursor: pointer;
}
@media screen and (max-width : 860px) {
    .project{
        display: flex;
        flex-direction: column;
    }
    .datainput{
        margin-left: 10%;
    }
}