.profile img{
    width : 200px;
    margin-top: 5%;;
    margin-right : 10%;
    margin-left: 8%;
}
.profile{
    
    grid-column-start: 2;
    grid-row-start: 1;
}

.detail{
    display: flex;
    margin-top: 5%;
    width : 90%;
    margin-left: 5%;
}
.userName{
    margin-top: 5%;
    margin-left: 10%;
}

.settingInput{
    width : 15vw;
    height : 30px;
}

.panel{
    color : white;
    background-color: #7e22ce;  
    font-size : 15px; 
    font-weight: 400;
    width : 60%;
    height: 40px;
    margin-left: 5%;
    align-items: center;
    display: grid;
    grid-template-columns: 5% 75% 15% 5%;
}

.upgrad{
    background-color: white ;
    color: black;
    height : 70%;
    margin-left: 30%;
    border-radius: 10px;
    text-align: center;
}
.paneltext{
    margin-right: 30%;
    grid-column-start: 2;
}
@media screen and (max-width : 700px) {

    .detail{
        flex-direction: column;
    }
    .settingInput{
        width : 60%;
    }
    .profile{
        margin-left: 20%;
    }
    .upgrad{
        width : 100%;
    }
    .panel{
        width: 80%;
    }
}