.table {
  border: 1px solid;
  border-radius: 10px;
  margin-left: 7%;
  width : 90%;
}
.filecard{
    width: 98%;
    border : 2px soild red;
}
.row,.head {
    display: grid;
    grid-template-columns: 25% 25% 25% 25%;
    width : 100%;
    text-align: center;
    border-bottom: 2px  solid rgb(145, 145, 145);
    

}
.head{
    font-weight: 500;
}
.row{
    font-weight: 400;
}
.row p {
    height: 25px;
}
.row div {
    height: 25px;
}
 .editbutton{
    margin: 20px;
    margin-top: 6%;
}
@media screen and (max-width : 1180px) {
    .table {
        margin-left: 8%;
        width : 85vw;
      }
}
@media screen and (max-width : 900px) {
    .table {
        
        margin-left: 3%;
        width : 92%;
      }
}
@media screen and (max-width : 820px) {
    .table {
        margin-top: 20%;
        margin-left: 3%;
        width : 90%;
      }
}
@media screen and (max-width : 600px) {
    .table {
        margin-left: 3%;
        width : 90%;
      }
      
.row p {
    height: 50px;
}
.row div {
    height: 40px;
}
}
@media screen and (max-width : 480px) {
    .table {
        margin-top: 20%;
        margin-left: 3%;
        width : 100%;
      }
}