.Navbar{
    display : grid;
    grid-template-columns: 80px auto auto auto 10% 10% 10%;
    align-items: center;
    height : 55px;
    background-color: white;
    color: rgb(1, 1, 1);
    width : 100%;
}
.logo img{
    margin-top : 3%;
    width : 110px;
}
.home {
    grid-column-start: 5;
    color: black;
}
.home,.signIn,.Register{
    text-align: center;
    text-decoration: none;
    color:rgb(11, 11, 11);
    cursor : pointer;
}
.line {
    background-color: #7e22ce ;
    height: 2px;
    width : 98%;
    margin-left: 10px;;
}
.logo{
    grid-column-start: 2;
    font-size: 20px; 
}
.expandNavbarIcon{
    width : 70%;
    border-radius: 10px;
    background-color: white;
    display: none;
    margin-left : 0%;
}
.disable{
    display: none;
}
.showConent{
    display: flex;
}

@media screen and (max-width : 900px){
    .Navbar{
        width : 100%;
    }
    .line{
        width : 100%;
    }
}
@media screen and (max-width : 700px){
    .Navbar{
        display : grid;
        width : 100%;
        grid-template-columns:  10% 80% 10%;
        grid-template-rows: 100% 100% 100% 100%; 
        align-items: center;
        height : 50px;
        position: relative;
        transition: all 0.5 ease  ;
    }
    
    .showConent{
        display: none;
        border-color: none;
    }
    .expandNavbarIcon{
        display:block;
    }
    .home,.signIn,.Register{
        width : 100%;
        grid-column: span 3 ;
        color:rgb(0, 0, 0);
        position:relative;
        height: 95%;
        z-index: 10;
        text-align: center;
        transition: all 0.5 ease  ;
    }
    .bgcolor{
        background-color: white;
    }
    .home{
        grid-row-start: 2;
    }
    .signIn{
        grid-row-start: 3;
    }
    .Register{
        grid-row-start: 4;
    }
}

@media screen and (max-width : 550px){

    .expandNavbarIcon{
        width : 95%;
    }
}